<template>
 <view class="cart-container" v-if="cart.length !== 0">
    
    <my-address></my-address>
    <!-- 购物车商品列表的标题区域 -->
    <view class="cart-title">
      <!-- 左侧的图标 -->
      <uni-icons type="shop" size="18"></uni-icons>
      <!-- 描述文本 -->
      <text class="cart-title-text">购物车</text>
    </view>
  <!-- 商品列表区域 -->
  <!-- uni-swipe-action 是最外层包裹性质的容器 -->
  <uni-swipe-action>
    <block v-for="(goods, i) in cart" :key="i">
      <uni-swipe-action-item :right-options="options" @click="swipeActionClickHandler(goods)"  >
        <my-cartgoods :goods="goods" :show-radio="true" :show-num="true" @radio-change="radioChangeHandler" @num-change="numberChangeHandler"></my-cartgoods>
      </uni-swipe-action-item>
    </block>
  </uni-swipe-action>
  
   <!-- 结算区域 -->
      <my-settle></my-settle>
  </view>
  
    <!-- 空白购物车区域 -->
    <view class="empty-cart" v-else>
      <image src="/static/cart.png" class="empty-img"></image>
      <text class="tip-text">空空如也~</text>
    </view>
</template>

<script>

  import {
    mapState,
    mapMutations
  } from 'vuex'
    import badgeMix from '@/mixins/tabbar-badge.js'
  export default {
    mixins: [badgeMix],
    computed: {
      // 将 m_cart 模块中的 cart 数组映射到当前页面中使用
      ...mapState('m_cart', ['cart']),
    },
    data() {
      return {
        options: [{
          text: '删除', // 显示的文本内容
          style: {
            backgroundColor: '#ea0000' // 按钮的背景颜色
          }
        }]
      };
    },
    methods: {
      ...mapMutations('m_cart', ['updateGoodsState', 'updateGoodsCount','removeGoodsById']),
      // 商品的勾选状态发生了变化
      radioChangeHandler(e) {
        this.updateGoodsState(e)
      },
      // 商品的数量发生了变化
      numberChangeHandler(e) {
        // console.log(e,"改变了数字");
        this.updateGoodsCount(e)
      },
      // 点击了滑动操作按钮
      swipeActionClickHandler(goods) {
        this.removeGoodsById(goods.goods_id)
      },
      
    },
    onLoad() {
      console.log(this.total, "total");
    }
  }
</script>

<style lang="scss" scoped>
    .cart-title {
      height: 40px;
      display: flex;
      align-items: center;
      font-size: 14px;
      padding-left: 5px;
      border-bottom: 1px solid #efefef;
  
      .cart-title-text {
        margin-left: 10px;
      }
    }
    .cart-container {
      padding-bottom: 50px;
    }
    .empty-cart {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 150px;
    
      .empty-img {
        width: 90px;
        height: 90px;
      }
    
      .tip-text {
        font-size: 12px;
        color: gray;
        margin-top: 15px;
      }
    }
</style>
